import React from 'react';
import {
    Button,
    Spinner

} from '@vkontakte/vkui';

import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon24Dropdown from '@vkontakte/icons/dist/24/dropdown';
import Icon24Chevron from '@vkontakte/icons/dist/24/chevron';
import '../css/main.css';

var sprintf = require('sprintf-js').sprintf;

const shikimoriGetAnimeUrl = "https://sh.sovetromantica.com/api/animes/%s"

class SubscribeBlockMobile extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            animeId: props.anime.anime_id,
            animeName: props.anime.anime_name,
            animeNameRus: props.anime.anime_name_rus,
            shikiId: props.anime.shiki_id,
            subStatus: props.anime.sub_status,
            dubStatus: props.anime.dub_status,
            shikiResp: undefined,
            opened: false,
            showed: props.anime.showed,
            subSwitching: false,
            dubSwitching: false
        };
        this.click = this.click.bind(this);
        this.clickButton = this.clickButton.bind(this);
    }

    async click(e) {
        this.setState({opened: !this.state.opened})
        if (!this.state.opened && this.state.shikiResp === undefined && this.state.shikiId !== 0) {
            let resp = await fetch(sprintf(shikimoriGetAnimeUrl, this.state.shikiId));
            let json = await resp.json();
            this.setState({shikiResp: json});
            console.log(1);
            //Image.prefetch(json.image.original);
            console.log(2);
            console.log(json);
        }
    }

    async clickButton(e, type) {
        e.stopPropagation();
        if ((type === 0 && (this.state.subStatus !== -2 && this.state.subStatus !== -1)) || (type === 1 && (this.state.dubStatus !== -2 && this.state.dubStatus !== -1))) {
            switch (type) {
                case 0:
                    this.setState({subSwitching: true});
                    break;
                case 1:
                    this.setState({dubSwitching: true});
                    break;
            }
            let resp = await this.props.switch(this.state.animeId, type)
            if (resp.error_code === 0) {
                switch (type) {
                    case 0:
                        if (this.state.subStatus === 1 || this.state.subStatus === 0) {
                            this.setState({subStatus: 1 - this.state.subStatus, subSwitching: false})
                        }
                        break;
                    case 1:
                        if (this.state.dubStatus === 1 || this.state.dubStatus === 0) {
                            this.setState({dubStatus: 1 - this.state.dubStatus, dubSwitching: false})
                        }
                        break;

                }
            }

        }


    }

    getButtonStyle(status, switching) {
        if (switching) {
            return "secondary";
        }
        switch (status) {
            case -1:
                return "secondary";
            case 0:
                return "primary";
            case 1:
                return "outline";

        }
    }

    getBeforeSymbol(status, switching) {
        if (switching) {
            return <Spinner size={"small"}/>;
        }
        switch (status) {
            case 1:
                return <Icon16Done/>;

        }
    }

    render() {
        if (this.state.showed) {
            return (
                <div>
                    <div className="subscribeRow" onClick={this.click}>
                        <div className="subscribeIconExtender">
                            {this.state.opened ? <Icon24Dropdown fill="var(--accent)"/> :
                                <Icon24Chevron fill="var(--accent)"/>}
                        </div>
                        <div className="subscribeRowFullContent">
                            <div className="subscribeRowContent">
                                <div
                                    className="subscribeRowTitleNameContainer">{this.state.animeName} / {this.state.animeNameRus}</div>
                                <div className="subscribeRowButtonContainer">
                                    <Button stretched
                                            level={this.getButtonStyle(this.state.subStatus, this.state.subSwitching)}
                                            before={this.getBeforeSymbol(this.state.subStatus, this.state.subSwitching)}
                                            onClick={(e) => this.clickButton(e, 0)}
                                            className="subscribeRowButton">Субтитры</Button>
                                    <Button stretched
                                            level={this.getButtonStyle(this.state.dubStatus, this.state.dubSwitching)}
                                            before={this.getBeforeSymbol(this.state.dubStatus, this.state.dubSwitching)}
                                            onClick={(e) => this.clickButton(e, 1)}
                                            className="subscribeRowButton">Озвучка</Button>

                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="fullTitleInformation" style={{"display":(this.state.opened)?"flex":"none", "height":"250px"}}>
                        <div className="fullTitleInformationContainer">
                            <div className="fullTitleInformationImage">
                                <Spinner size="large" />
                            </div>
                        </div>

                    </div>
                </div>

            );
        }
    }
}

export default SubscribeBlockMobile;