import React from 'react';
import {
    Button,
    Spinner,
    Link

} from '@vkontakte/vkui';

import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon24Dropdown from '@vkontakte/icons/dist/24/dropdown';
import Icon24Chevron from '@vkontakte/icons/dist/24/chevron';
import '../css/main.css';
import connect from '@vkontakte/vkui-connect'

var sprintf = require('sprintf-js').sprintf;

const shikimoriGetAnimeUrl = "https://sh.sovetromantica.com/api/animes/%s"
const shikimoriImageBaseUrl = "https://shikimori.one%s"
const siteBaseUrl = "https://sovetromantica/com/anime/%s"
const vkBaseUrl = "https://vk.com/%s"

class SubscribeBlock extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            animeId: props.anime.anime_id,
            animeName: props.anime.anime_name,
            animeNameRus: props.anime.anime_name_rus,
            shikiId: props.anime.shiki_id,
            subStatus: props.anime.sub_status,
            dubStatus: props.anime.dub_status,
            shikiResp: undefined,
            opened: false,
            showed: props.anime.showed,
            subSwitching: false,
            dubSwitching: false,
            titleImageUrl: '',
            titleDesc: '',
            titleGenres: '',
            titleShikimoriLink:'',
            titleSiteLink:'',
            translators: props.anime.translator,
            editors: props.anime.editor,
            dubbers: props.anime.sounder,
            timers: props.anime.timer,
        };

        this.click = this.click.bind(this);
        this.clickButton = this.clickButton.bind(this);
    }

    async click(e) {
        this.setState({opened: !this.state.opened})
        if (!this.state.opened && this.state.shikiResp === undefined && this.state.shikiId !== 0) {
            let resp = await fetch(sprintf(shikimoriGetAnimeUrl, this.state.shikiId));
            let json = await resp.json();
            var genres=[];
            for (let el in json.genres){
                genres.push(json.genres[el].russian)
            }
            this.setState({
                shikiResp: json,
                titleImageUrl: sprintf(shikimoriImageBaseUrl, json.image.preview),
                titleDesc:json.description_html.replace(/<[^>]*>/gm,''),
                titleGenres: genres.join(', '),
                titleShikimoriLink: sprintf(shikimoriImageBaseUrl, json.url),
                titleSiteLink: sprintf(siteBaseUrl, this.state.animeId),
            });
        }
    }

    async clickButton(e, type) {
        try{
            e.stopPropagation();
        } catch(ex){}
        if ((type === 0 && (this.state.subStatus !== -2 && this.state.subStatus !== -1)) || (type === 1 && (this.state.dubStatus !== -2 && this.state.dubStatus !== -1))) {
            switch (type) {
                case 0:
                    this.setState({subSwitching: true});
                    break;
                case 1:
                    this.setState({dubSwitching: true});
                    break;
            }
            let resp = await this.props.switch(this.state.animeId, type)
            if (resp.error_code === 0) {
                switch (type) {
                    case 0:
                        if (this.state.subStatus === 1 || this.state.subStatus === 0) {
                            this.setState({subStatus: 1 - this.state.subStatus, subSwitching: false})
                        }
                        break;
                    case 1:
                        if (this.state.dubStatus === 1 || this.state.dubStatus === 0) {
                            this.setState({dubStatus: 1 - this.state.dubStatus, dubSwitching: false})
                        }
                        break;

                }
            }
            else if (resp.error_code===7){
                connect.subscribe((e)=>{
                    switch(e.detail.type){
                        case 'VKWebAppAllowMessagesFromGroupResult':
                            if(e.detail.data.result){
                                this.clickButton(e, type);
                            }
                            break;
                        case 'VKWebAppAllowMessagesFromGroupFailed':
                            switch (type) {
                                case 0:
                                    this.setState({subSwitching: false});
                                    break;
                                case 1:
                                    this.setState({dubSwitching: false});
                                    break;
                            }
                            break;
                    }
                });
                connect.send("VKWebAppAllowMessagesFromGroup", {"group_id": 33905270});
            }
            else {
                console.log(resp);
            }

        }


    }

    getButtonStyle(status, switching) {
        if (switching) {
            return "secondary";
        }
        switch (status) {
            case -1:
                return "secondary";
            case 0:
                return "primary";
            case 1:
                return "outline";

        }
    }

    getBeforeSymbol(status, switching) {
        if (switching) {
            return <Spinner size={"small"}/>;
        }
        switch (status) {
            case 1:
                return <Icon16Done/>;

        }
    }

    render() {
        if (this.state.showed) {
            return (
                <div>
                    <div className="subscribeRow" onClick={this.click}>
                        <div className="subscribeIconExtender">
                            {this.state.opened ? <Icon24Dropdown fill="var(--accent)"/> :
                                <Icon24Chevron fill="var(--accent)"/>}
                        </div>
                        <div className="subscribeRowFullContent">
                            <div className="subscribeRowContent">
                                <div
                                    className="subscribeRowTitleNameContainer">{this.state.animeName} / {this.state.animeNameRus}</div>
                                <div className="subscribeRowButtonContainer">
                                    <Button stretched
                                            level={this.getButtonStyle(this.state.subStatus, this.state.subSwitching)}
                                            before={this.getBeforeSymbol(this.state.subStatus, this.state.subSwitching)}
                                            onClick={(e) => this.clickButton(e, 0)}
                                            className="subscribeRowButton">Субтитры</Button>
                                    <Button stretched
                                            level={this.getButtonStyle(this.state.dubStatus, this.state.dubSwitching)}
                                            before={this.getBeforeSymbol(this.state.dubStatus, this.state.dubSwitching)}
                                            onClick={(e) => this.clickButton(e, 1)}
                                            className="subscribeRowButton">Озвучка</Button>

                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="fullTitleInformation" style={{"display": (this.state.opened) ? "flex" : "none"}}>

                        <div style={{"width": "25px", "height": "100%", "display": "flex"}}>
                        </div>
                        <div style={{"width":"100%","display":"flex", "justifyContent":"space-around"}}>
                            {this.state.shikiResp===undefined && <Spinner size="large" className="fullTitleInformationSpinner"/>}
                            <div className="fullTitleInformationContainer" style={{"display":(this.state.shikiResp===undefined)?"none":null}}>

                                <div className="fullTitleInformationImage" >
                                    <img src={this.state.titleImageUrl} className="fullTitleInformationImageImg"/>
                                </div>
                                <div className="fullTitleInformationContent">
                                    {this.state.titleDesc!=='' && <div><b>Описание: </b>{this.state.titleDesc}</div>}
                                    {this.state.titleDesc!=='' && <hr/>}
                                    {this.state.titleGenres!=='' && <div><b>Жанры: </b>{this.state.titleGenres}</div>}
                                    {this.state.titleGenres!=='' && <hr/>}
                                    {this.state.translators!=null && <div><b>Переводчики: </b>{this.state.translators.map(item=>(
                                        (item.show_vk===1)?<Link href={sprintf(vkBaseUrl, item.vk_link)} className="fullTextInformationAuthorLink">{item.nickname}</Link>:item.nickname
                                    )).reduce((prev, curr) => [prev, ', ', curr])}</div>}
                                    {this.state.editors!=null && <div><b>Редакторы: </b>{this.state.editors.map(item=>(
                                        (item.show_vk===1)?<Link href={sprintf(vkBaseUrl, item.vk_link)} className="fullTextInformationAuthorLink">{item.nickname}</Link>:item.nickname
                                    )).reduce((prev, curr) => [prev, ', ', curr])}</div>}
                                    {this.state.dubbers!=null && <div><b>Дабберы: </b>{this.state.dubbers.map(item=>(
                                        (item.show_vk===1)?<Link href={sprintf(vkBaseUrl, item.vk_link)} className="fullTextInformationAuthorLink">{item.nickname}</Link>:item.nickname
                                    )).reduce((prev, curr) => [prev, ', ', curr])}</div>}
                                    {this.state.timers!=null && <div><b>Таймеры: </b>{this.state.timers.map(item=>(
                                        (item.show_vk===1)?<Link href={sprintf(vkBaseUrl, item.vk_link)} className="fullTextInformationAuthorLink">{item.nickname}</Link>:item.nickname
                                    )).reduce((prev, curr) => [prev, ', ', curr])}</div>}
                                    {(this.state.translators!=null || this.state.editors!=null || this.state.dubbers!=null || this.state.timers!=null) && <hr/>}
                                    <div className="fullTitleInformationContentButton">
                                        <Button stretched href={this.state.titleShikimoriLink} component="a" className="subscribeRowButton">Shikimori</Button>
                                        <Button stretched href={this.state.titleSiteLink} component="a" className="subscribeRowButton">Сайт</Button>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            );
        }
    }
}

export default SubscribeBlock;