import React from 'react';
//import connect from '@vkontakte/vkui-connect';
import {
    View,
    Root,
    FixedLayout, Search, Group, Div, Panel

} from '@vkontakte/vkui';
import {platform, IOS, ANDROID} from '@vkontakte/vkui';

import '@vkontakte/vkui/dist/vkui.css';
import queryString from 'query-string';
import Header from './common/Header'
import connect from '@vkontakte/vkui-connect'
import SubscribeBlock from "./common/SubscribeBlock";
import SubscribeBlockMobile from "./common/SubscribeBlockMobile";
import StatisticBlock from "./common/StatisticBlock";
//import PanelSpinner from '@vkontakte/vkui/src/components/PanelSpinner/PanelSpinner'

var sprintf = require('sprintf-js').sprintf;


const reqSubscribeList = 'https://api.sovetromantica.com/api/v1/notify/vkapp/%s/get';
const switchSubscribe = 'https://api.sovetromantica.com/api/v1/notify/vkapp/%s/switch/%s/%s';
const reqStatisticUrl = 'https://api.sovetromantica.com/api/v1/notify/vkapp/statistic/%s';

class App extends React.Component {
    constructor(props) {
        super(props);
        let params = queryString.parse(window.location.search);
        let data = new FormData();
        for (let par in params) {
            data.append(par, params[par]);
        }
        this.state = {
            mode: 'subscribe',
            rights: params.vk_viewer_group_role,
            userId: params.vk_user_id,
            clientType: params.vk_platform,
            subscribeTitles: [],
            urlData:data,
            loading:true,
        };
        this.selectScreen = this.selectScreen.bind(this);

    }


    selectScreen(e) {
        this.setState({mode:e.currentTarget.dataset.mode});
    }

    componentDidMount() {
        fetch(sprintf(reqSubscribeList, this.state.userId), {method: 'POST', body: this.state.urlData})
            .then(resp => resp.json())
            .then((result) => {
                for(let el in result){
                    result[el].showed=true;
                }
                this.setState({subscribeTitles: result, loading:false});
            });

    }

    async switchSubscribeStatus(titleId, titleType){
        if(titleId!==0 && titleId!==undefined && titleType!==undefined){
            let resp= await fetch(sprintf(switchSubscribe, this.state.userId, titleId, titleType), {method: 'POST', body: this.state.urlData});
            let json=await resp.json();
            return json;
        } else {
            console.log("switch parameters are wrong")
        }
    }

    async loadStatistic(minimumId){
        if(minimumId==undefined){
            minimumId=0;
        }
        let resp= await fetch(sprintf(reqStatisticUrl, minimumId), {method: 'POST', body: this.state.urlData});
        let json= await resp.json();
        return json;
    }


    render() {
        return (
            <Root activeView="sub-title">
                <View id="sub-title" activePanel={this.state.mode}>
                    <Panel id="subscribe">

                        <Header rights={this.state.rights} mode={this.state.mode} select={this.selectScreen} client={this.state.clientType}/>

                        {this.state.subscribeTitles.map(item => (
                            (this.state.clientType!=='desktop_web')?<SubscribeBlock anime={item} switch={this.switchSubscribeStatus.bind(this)}/>:<SubscribeBlock anime={item} switch={this.switchSubscribeStatus.bind(this)}/>

                        ))}
                    </Panel>
                    <Panel id="statistic_full">
                        <Header rights={this.state.rights} mode={this.state.mode} select={this.selectScreen}/>
                        <StatisticBlock minimumId="0" loader={this.loadStatistic.bind(this)} />
                    </Panel>
                    <Panel id="statistic_cur">
                        <Header rights={this.state.rights} mode={this.state.mode} select={this.selectScreen}/>
                        <StatisticBlock minimumId="797" loader={this.loadStatistic.bind(this)} />
                    </Panel>
                </View>
            </Root>

        );
    }
}

export default App;
