import React from 'react';
import {
    Progress

} from '@vkontakte/vkui';

class StatisticBlock extends React.Component {


    constructor(props) {
        super(props);
        this.state= {
            minimumId: this.props.minimumId,
            statData: undefined,
            maxVotes: 0
        };
    }
    async componentDidMount() {
        let json=await this.props.loader(this.state.minimumId);
        if (Array.isArray(json)){
            json.sort(function(a,b){return b.total-a.total;});
            this.setState({statData:json, maxVotes:json[0].total});
        } else {

        }

    }
    render() {
        return (
            this.state.statData!=undefined &&
                this.state.statData.map((item)=>(
                    <div className="subscribeRow" onClick={this.click}>
                        <div className="subscribeRowFullContent">
                            <div className="subscribeRowContent">
                                <div
                                    className="subscribeRowTitleNameContainer">{item.anime_name}</div>
                                <div className="statisticRowProgressContainer">
                                    <Progress value={100*item.total/this.state.maxVotes}/>
                                </div>
                            </div>
                        </div>
                    </div>
                ))

        );
    }
}

export default StatisticBlock